import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpService } from 'src/app/services/http.service';
import { AlertService } from 'src/app/services/alert.service';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  public type: any;
  public userLoggedIn: any;
  submitted: boolean = false;

  constructor(
    private router: Router,
    private formBuilder: FormBuilder,
    private commonService: HttpService,
    private alert: AlertService,
    private data: DataService
  ) { }

  ngOnInit() {
    this.buildForm();
    this.userLoggedIn = this.commonService.getUserData();
    if (this.userLoggedIn != undefined) {
      this.alreadyLoggedIn()
    }
  }

  buildForm() {
    this.loginForm = this.formBuilder.group({
      mobile_number: ['', [Validators.required, Validators.pattern("^[0-9]*$")]],
      password: ['', [Validators.required]],
    });
  }

  get f() { return this.loginForm.controls; }

  loginNow() {
    this.submitted = true;
    console.log(this.loginForm.invalid);
    if (this.loginForm.invalid) {
      return false;
    }
    const req_url = "auth/login";
    this.data.showLoader(true);
    var req_data = this.loginForm.value;
    this.commonService.commonPostCall(req_url, req_data)
      .subscribe(
        res => {
          const datas = res.data.user;
          this.data.showLoader(false);
          this.alert.success(res.message);
          this.commonService.shareUserData(res.data.user);
          const userData = JSON.stringify(datas);
          const localConfigData = {
            userData: userData,
            token: res.data.accessToken.token,
            uid: datas.uid,
          };
          this.commonService.setLocalConfig(localConfigData);
          this.router.navigate(['/user/dashboard']);
        },
        err => {
          this.data.showLoader(false);
          if (err.status == 0) {
            this.alert.error('Check your internet connection!')
          } else {
            this.alert.error(err.error.message);
          }
        }
      );
  }

  alreadyLoggedIn() {
    const req_url = "auth/already_logged_in";
    this.commonService.commonGetCall(req_url)
      .subscribe(
        res => {
          this.router.navigate(['/user/dashboard']);
        },
        err => {
          console.log(err);
        }
      );
  }
}
