import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http.service';
import { AlertService } from 'src/app/services/alert.service';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-my-account',
  templateUrl: './my-account.component.html',
  styleUrls: ['./my-account.component.css']
})
export class MyAccountComponent implements OnInit {
  member_id: string = 'NA';
  full_name: string = 'NA';
  dob: string = 'NA';
  national_number: string = 'NA';
  id_type: string = 'NA';
  gender: string = 'NA';
  mobile_number: string = 'NA';
  email: string = 'NA';
  signature: any = 'NA';

  constructor(
    private commonService: HttpService,
    private alert: AlertService,
    private data: DataService
  ) { }

  ngOnInit(): void {
    const req_url = "user/account";
    this.data.showLoader(true);
    this.commonService.commonGetCall(req_url)
      .subscribe(
        res => {
          const user = res.data;
          this.member_id = user.membership_id;
          this.full_name = user.first_name + ' ' + user.family_name;
          this.dob = user.dob;
          this.gender = user.gender === 1 ? 'Male' : 'Female';
          this.mobile_number = user.mobile_number;
          this.email = user.email;
          this.id_type = user.id_type ? user.id_type : 'NA';
          this.national_number = user.national_number ? user.national_number : 'NA';
          this.data.showLoader(false);
        },
        err => {
          console.log(err)
          this.data.showLoader(false);
          if (err.status == 0) {
            this.alert.error('Check your internet connection!')
          } else if (err.status === 401) {
            this.alert.error(err.statusText);
          } else {
            this.alert.error(err.error.message);
          }
        }
      );
  }

}
